import { Component, ViewChild, Input } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Router } from '@angular/router';

@Component({
  selector: 'app-success-modal',
  templateUrl: 'success-modal.component.html'
})
export class SuccessModalComponent {
  @ViewChild('modal') modal: ModalDirective;

  public onCloseCallback: Function;
  public onCloseUrl: string;
  @Input() title: string = '';

  constructor(private router: Router) { }

  show(callback?: string | Function) {

    this.onCloseUrl = this.onCloseCallback = null;
    if (typeof callback === 'string') {
      this.onCloseUrl = callback;
    } else if (typeof callback === 'function') {
      this.onCloseCallback = callback;
    }

    this.modal.show();
  }

  hide(closeCallback?: boolean) {

    closeCallback = !!closeCallback;
    if (closeCallback) {
      this.onCloseUrl = null;
      this.onCloseCallback = null;
    }

    this.modal.hide();
  }

  onHidden() {

    if (this.onCloseUrl) {
      this.router.navigateByUrl(this.onCloseUrl);
    } else if (this.onCloseCallback) {
      this.onCloseCallback();
    }
  }
}
