import { Component, Input } from '@angular/core';
import { AbstractControlDirective, AbstractControl } from '@angular/forms';

@Component({
 selector: 'app-inline-error',
 template: `
   <div *ngIf="shouldShowErrors()" class="text-danger inline-errors">
     <small *ngFor="let error of listOfErrors()">{{error}}</small>
   </div>
 `,
})
export class InlineErrorComponent {

  private static readonly errorMessages = {
    'required': () => 'Required',
    'minlength': (params) => 'Minimum length: ' + params.requiredLength,
    'maxlength': (params) => 'Maximum length: ' + params.requiredLength,
    'pattern': (params) => 'Required pattern: ' + params.requiredPattern,
    'app-exactmatch': (params) => 'Must match: ' + params.requiredMatch,
    'app-minitems': (params) => 'Minimum items: ' + params.requiredItems,
    'app-maxitems': (params) => 'Maximum items: ' + params.requiredItems
  };

  @Input()
  private control: AbstractControlDirective | AbstractControl;

  shouldShowErrors(): boolean {

    return this.control && this.control.errors &&
      (this.control.dirty || this.control.touched);
  }

  listOfErrors(): string[] {

    return Object.keys(this.control.errors)
      .map(field => this.getMessage(field, this.control.errors[field]));
  }

  private getMessage(type: string, params: any) {
    return InlineErrorComponent.errorMessages[type](params);
  }

};
